 <!--
 * @Description: 操作日志
 * @LastEditors: Ji Yinghao
 * @Date: 2019-04-30 12:52:55
 * @LastEditTime: 2019-10-09 18:47:50
 -->

<template>
  <div>
    <!-- 导航 -->
    <Breadcrumbs :items="items"/>
    <!-- 数据表格 -->
    <v-card>
      <v-card-title>
        <v-layout row wrap>
          <v-flex xs6 sm4 md2>
            <v-select
              @change="info"
              menu-props="auto"
              v-model="journal"
              :items="['查询操作', '敏感操作']"
              label="*日志类型"
              required
            ></v-select>
          </v-flex>
          <v-flex xs6 sm4 md2>
            <v-text-field v-model="user_name" label="账号"></v-text-field>
          </v-flex>
          <v-btn :loading="loader" :disabled="loader" color="primary" @click="info(false)">筛选日志</v-btn>
        </v-layout>
      </v-card-title>
      <!-- 
        headers : 表头
        items： 表格内容
        total-items ： 总共的数据数量 （手动设置项目的总数，就可以禁用内置排序和分页）
        disable-initial-sort 在初始渲染时禁用默认排序
        :hide-actions="true" 隐藏表格操作
        :dark="true" 暗主题
        :loading="true" 加载进度条
      -->
      <v-data-table
        :headers="headers"
        :items="desserts"
        class="elevation-1"
        :total-items="allList"
        :rows-per-page-text="`每页行数`"
        :rows-per-page-items="pagingList"
        :disable-initial-sort="true"
        :loading="loader"
        @update:pagination="info"
      >
        <template v-slot:items="props">
          <tr @dblclick="infoMessage( props.item )">
          <td>{{ props.index+1 }}</td>
          <td class="text-xs-centre">{{ props.item.user_name }}</td>
          <td class="text-xs-centre">{{ props.item.create_time | time }}</td>
          <td class="text-xs-centre">{{ props.item.descriptor }}</td>
          <td class="text-xs-centre">{{ props.item.main_menu }} ^ {{ props.item.content | slice }}</td>
          <td class="text-xs-centre">
             <v-btn text icon @click="infoMessage( props.item )" >
              <v-icon color="primary"  >visibility</v-icon>
            </v-btn>
            <!-- <v-btn small color="primary" @click="infoMessage( props.item )" >详情</v-btn> -->
          </td>
          </tr>
        </template>
        <!-- 当没有数据的时候，可以使用 no-data 插槽显示一段自定义HTML。 -->
        <template v-slot:no-data>
          <!-- <v-alert :value="true" color="warning" icon="priority_high" outline>没有更多数据了 :(</v-alert> -->
          <p class="p_text">没有更多数据了 :(</p>
        </template>
        <!-- 如果你还想添加一些额外的功能，比如每列过滤或者搜索，你可以使用 footer 插槽。 -->
        <!-- <template v-slot:footer>
          <td :colspan="headers.length">
            <strong>总计：</strong>
          </td>
        </template>-->
        <!-- 你可以使用page-text 插槽来自定义显示范围以及总条目。 -->
        <template
          v-slot:pageText="props"
        >当前 {{ props.pageStart }} - {{ props.pageStop }} 总共 {{ props.itemsLength }}</template>
      </v-data-table>
    </v-card>
    <!-- 详情框 -->
    <Module1 ref="module1" ></Module1>
  </div>
</template>

<script>
import Breadcrumbs from "../../components/breadcrumbs/breadcrumbs.vue";
import Module1 from "./components/module1.vue";
export default {
  components: {
    Breadcrumbs,
    Module1
  },
  data() {
    return {
      user_name:"",
      // 操作日志
      journal: "查询操作",
      pagingList: [10, 20, 30, 40, 50, { text: "100", value: 100 }],
      loader: false,
      page: 0,
      page_count: 10,
      allList: 0,
      items: [
        {
          text: "首页",
          disabled: false,
          to: "/home"
        },
        {
          text: "操作日志",
          disabled: false,
          to: "/operationLog"
        }
      ],
      headers: [
        {
          text: "#",
          align: "left",
          sortable: false, // 是否可排序
          value: "name"
        },
        { text: "操作者账号", value: "calories", sortable: false },
        { text: "操作时间", value: "fat", sortable: false },
        { text: "描述信息", value: "carbs", sortable: false },
        { text: "传参", value: "carbs", sortable: false },
        { text: "操作", value: "carbs", sortable: false }
      ],
      desserts: []
    };
  },
  // 更新完成
  mounted: function() {
    // this.info();
  },
  methods: {
    // 查看账号详情
    infoMessage( data ){
      let datas = {
        state:true,
        list: data
      }
      this.$refs.module1.dialogShow(datas);
    },
    loader_() {
      this.loader = true;
      setTimeout(() => (this.loader = false), 500);
    },
    info(event) {
      this.loader = true;
      let URL_ = "";
      this.journal == "查询操作"
        ? (URL_ = this.$apis.URL.manages_operation_log)
        : this.journal == "敏感操作"
        ? (URL_ = this.$apis.URL.manages_query_warning_log)
        : (URL_ = this.$apis.URL.manages_operation_log);
      this.$axios({
        method: "post",
        url: URL_,
        data: {
          "user_name": this.user_name,
          "page": event==false?this.page:event.page-1,
          "page_count": event==false?this.page_count:event.rowsPerPage
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.desserts = response.data.datas.list;
            this.allList = response.data.datas.total_number;
          } else {
            this.$Dialog.getProductCheckResult_({
                text: response.data.message,
                timeout: 3000,
                snackbar: true
            })
          }
          this.loader = false;
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({
                text: error,
                timeout: 3000,
                snackbar: true
            })
        });
    }
  },
  watch: {}
};
</script>

<style lang="less" scoped>
  .flex {
    padding-right: 10px;
    padding-left: 10px;
  }
</style>

