<!--
 * @Description: 账号详细信息弹出框模块
 * @LastEditors: Ji Yinghao
 * @Date: 2019-05-05 13:42:39
 * @LastEditTime: 2019-09-11 17:42:35
 * @persistent: 点击背景不可关闭
 
 -->
<template>
  <div >
    <v-layout row justify-center>
      <v-dialog v-model="dialog"  max-width="1000px">
        <v-card>
          <v-card-title>
            <span class="headline">账号详细信息</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <p class="root_p" > 操作账号 <v-chip>{{ data.user_name }}</v-chip> </p>
              <p class="root_p" > 操作者ID <v-chip> {{ data.operator_id }} </v-chip> </p>
              <p class="root_p" > 描述信息 <v-chip>{{ data.descriptor  }}</v-chip> </p>
              <p class="root_p" > 接口信息 <v-chip>{{ data.main_menu }}</v-chip> </p>
              <p class="root_p" > 参数信息 &nbsp; <span class="red--text text--lighten-1" > {{ data.content }} </span></p>
              <p class="root_p" > 操作时间 <v-chip>{{ data.create_time | time }}</v-chip> </p>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <!-- <v-btn color="blue darken-1" flat @click="dialog = false">取消</v-btn> -->
            <v-btn color="info darken-1" @click="dialog = false">确认</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialog: false,
        data:{
            create_time: "",
            email: "",
            gender: "",
            id: "",
            order_sn: "",
            phone: "",
            role_id: "",
            role_name: "",
            status: "",
            true_name: "",
            update_time: "",
            user_name: ""
        }
    };
  },
  // 更新完成
  mounted: function() {
    
  },
  methods: {
    dialogShow(data){
        this.dialog = data.state;
        this.data = data.list;
    }
  },
};
</script>

<style lang="less" scoped>

.root_p{
    font-size: 16px;
}
</style>
